    loadInterests() { //사용자 관심 경매 조회
      this.$http.post('/api/user/interests', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.interestlist = res.data

      }).catch(err => {
        alert(`${err} in interestList function`)
      })
    },    
    loadBids() { //사용자 입찰 목록 조회
      this.$http.post('/api/user/bids', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.bidlist = res.data
      }).catch(err => {
        alert(`${err} in bidList function`)
      })
    },       
    loadProducts () { // 위에서 출력할때 쓰일 함수
      this.$http.post('/api/products/').then(res => {
        this.productlist = res.data
      }).catch(err => {
        alert(`${err} in show function`)
      })
    },
    showModal (id) { // 클릭한 id를 저장하는 함수
      this.ids = [] // 우선 이전의 기록을 지우기 위해 배열 초기화
      this.ids.push(id) // 클릭된 id를 push
    },
    isItemClicked (id) { // 해당 id가 클릭되었는지 검사하는 함수
      let test = false // 우선 false로 초기화
      this.ids.forEach(i => {
        if (i === id) { // 만약 i와 id가 같다면
          test = true // true로 고치기
          this.selected_item = id
          return test // test값 반환 (true이면 위의 if문에서 모달이 제대로 출력됨)
        }
      })
      return test
    },

    loadOpened() { //내가 연 경매 목록
      this.$http.post('/api/user/owned', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.openedlist = res.data

      }).catch(err => {
        alert(`${err} in openedList function`)
      })
    },  

    loadDepositWinningPrice() { //낙찰금 송금해야하는 경매
      this.$http.post('/api/user/bids/winning', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.depositWinningPricelist = res.data

      }).catch(err => {
        alert(`${err} in depositWinningPriceList function`)
      })
    }, 
    
    loadWaitRefund() { // 보증금 환불 대기 경매
      this.$http.post('/api/user/bids/withdrawing', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.waitRefundlist = res.data

      }).catch(err => {
        alert(`${err} in waitRefundList function`)
      })
    }, 
    
    loadBidsFinished() { // 입찰한 경매중 종료된 경매
      this.$http.post('/api/user/bids/ended', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.bidsFinishedlist = res.data

      }).catch(err => {
        alert(`${err} in bidsFinishedList function`)
      })
    }, 
    
    loadGetSuccessMoney() { // 낙찰금 입금 대기 경매
      this.$http.post('/api/user/owned/winning', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.getSuccessMoneylist = res.data

      }).catch(err => {
        alert(`${err} in getSuccessMoneyList function`)
      })
    }, 

    loadWaitDepositWithdraw() { // 보증금 환불이 완료되기를 기다리고있는 경매
      this.$http.post('/api/user/owned/withdrawing', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.waitDepositWithdrawlist = res.data

      }).catch(err => {
        alert(`${err} in waitDepositWithdrawList function`)
      })
    }, 

    loadOpenedFinished() { // 연 경매중 종료된 경매
      this.$http.post('/api/user/owned/ended', {
        user_id: this.$session.get('user')
      }).then(res => {
        this.openedFinishedlist = res.data

      }).catch(err => {
        alert(`${err} in openedFinishedList function`)
      })
    },

    getParticipantId() {
      return new Promise((resolve, reject) => {
        this.$http.post('/api/auction/participate/id', {
          user_id: this.$$session.get('user'),
          item_id: this.selected_item
        }).then(res => {
          if (res.data.success) {
            resolve(res.data.participant_id)
          } else {
            throw new Error("getParticipantId - request failed")
          }
        }).catch(err => {
          reject(err)
        })
      })
    },
    
    async depositToOwner(){ //낙찰자가 오너에게 낙찰금 보낸다.
      const participant_id = await this.getParticipantId()
      this.$http.post('/api/auction/successful', {
        user_id: this.$session.get('user'),
        item_id: this.selected_item,
        participant_id: participant_id
      }).then(res => {
        if (res.data.success) {
          this.successAlert_depositToOwner()
          return res.data
        }
      }).catch(err => {
        this.failAlert_depositToOwner()
      })
    },

    async getRefund(){ //보증금 환불이 진행되는 함수
      const participant_id = await this.getParticipantId()
      this.$http.post('/api/auction/withdraw', {
        user_id: this.$session.get('user'),
        item_id: this.selected_item,
        participant_id: participant_id
      }).then(res => {
        if (res.data.success) {
          this.successAlert_getRefund()
          return res.data
        }
      }).catch(err => {
        this.failAlert_getRefund()
      })
    },

    finishAuction() { //경매 종료하기 누르면 실행되는 함수
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      }

      if (1){ //경매 종료가 정상적으로 이루어졌을 경우       
        this.successAlert_finish()
        this.$bvModal.hide('modal-multi-17')
        this.$bvModal.hide('modal-multi-18')
      }
      else{
        this.failAlert_finish()
        this.$bvModal.hide('modal-multi-17')
        this.$bvModal.hide('modal-multi-18')
      }

    },

    deleteFavorite() { //관심 경매 버튼 클릭시 실행되는 함수
      this.$confirm("정말 삭제하시겠습니까?")
      .then(() => {
        this.$http.post('/api/interests/delete', {
        user_id: this.$session.get('user'),
        item_id: this.selected_item
        }).then(res => {
          if (res.data.success) {
            this.loadInterests()
          } else {
            this.failAlert_delete()
          }
        }).catch(err => {
          alert(`${err} in deleteFavorite function`)
        })
      })
    },   
    failAlert_delete() {
      this.$fire({
        title: "관심경매 삭제에 실패했습니다.",
        text: "다시 시도해보세요.",
        type: "error",
        timer: 3000
      })
    },        
    resetModal () {
      this.bid_price = ''
      this.bid_priceState = null
    },
      resetModal () {
      this.bid_price = ''
      this.bid_priceState = null
    },

    async bidding(index, item) {     // 입찰 함수
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      }

      const now_date = moment().format('YYYY-MM-DD HH:mm:ss')
      if (now_date >= item.close_date) {
        //console.log('1')
        this.failAlert_expiredbid()
      }

      if (this.bid_price <= item.max_bid) {
        //console.log('2')
        this.failAlert_maxbid()
      }

      if (this.bid_price <= item.reserve_price) {
        //console.log('3')
        this.failAlert_reservebid()
      }      
       
      if (this.checkDeposit()) {   // 보증금 체크
        //console.log('4')
        this.depositMoney()
      }

      const participant_id = await this.getParticipantId()
      this.$http.post('/api/auction/bid',{
        user_id: this.$session.get('user'),
        item_id: this.selected_item,
        participant_id: participant_id,
        value: this.bid_price,
        address: '1111' // 여기에 참여자가 보증금 낸 지갑주소 적어주셈
      }).then(res => {
        if (res.data.success) {
          this.$set(this.productlist[index], 'max_bid', this.bid_price)
          this.successAlert_bid()
          this.$bvModal.hide('modal-multi-11')
          this.$bvModal.hide('modal-multi-12')
        } else {
          this.failAlert_bid()
        }
      }).catch(err => {
        this.failAlert_bid()
      })
    },

    addFavorite() { //관심 경매 버튼 클릭시 실행되는 함수
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      } 
      this.$http.post('/api/interests/register',{
        user_id: this.$session.get('user'),
        item_id: this.selected_item
      }).then(res => {
        if (res.data.success) {
          this.hideModals()
          this.successConfirm_interest()
        } else {
          this.failAlert_interest_overlapped()
        }
      }).catch(err => {
        //alert(err)
        this.failAlert_interest()
      })
    },    

    async depositMoney(index, product_id, contract_num, product_price) { //경매 참여하기 누르면 실행되는 함수
      /* 아래는 하영이 코드가 미완성이라, 프론트 단 보증금 테스트를 위해 임시적으로 짠 것임*/ 
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      } 
      try {
        const res = await this.participate(product_id)
        //console.log(res.data)
      } catch (err) {
        console.log(err)
