        //   }
      }
        else{
          return false 
       }                
      })
    }, 
    participate() {   // 경매 참여자 DB 저장 함수
      this.$http.post('/api/auction/participate', {
        participate: {
          user_id: this.$session.get('user'),
          item_id: this.selected_item
        }
      }).then(res => {
        if (res.data.success) {
          return true
        }
      }).catch(err => {
        this.failAlert_deposit()
        return false
      })
    },
    successAlert_bid() {
      this.$fire({
        title: "Bidding completed!",
        type: "success",
        timer: 3000
      })
    },
    failAlert_bid() {
      this.$fire({
        title: "Failed bidding",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },    
    failAlert_maxbid() {
      this.$fire({
        title: "Failed bidding",
        text: "The price should exceed current highest price",
        type: "error",
        timer: 3000
      })
    },
    failAlert_reservebid() {
      this.$fire({
        title: "Failed bidding",
        text: "The price is lower than reserved price",
        type: "error",
        timer: 3000
      })
    },        
    failAlert_expiredbid() {
      this.$fire({
        title: "Expired Auction",
        text: "The auction is overdue",
        type: "error",
        timer: 3000
      }).then(() => location.reload())
    },         
    successConfirm_interest() {
      this.$fire({
        title: "Add to Favorites completedd!",
        type: "success",
        confirmButtonText: 'Go to Favorites',
        showCancelButton: true
      }).then(result => {
        if (result.value) {
          this.$router.push('/mypage')
        }
      })
    },
    successAlert_deposit() {
      this.$fire({
        title: "Deposit completed!",
        type: "success",
        timer: 3000
      })
    },
    failAlert_interest_overlapped() {
      this.$fire({
        title: "Already in Favorites!",
        type: "error",
        timer: 3000
      })
    },
    failAlert_interest() {
      this.$fire({
        title: "Add to Favorites failed",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },
    failAlert_deposit() {
      this.$fire({
        title: "Failed to deposit",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },
    successAlert_depositToOwner(){
      this.$fire({
        title: "Deposit to Owner Completed!",
        type: "success",
        timer: 3000
      })
    },

    failAlert_depositToOwner(){
      this.$fire({
        title: "Failed to deposit to Owner",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },

    successAlert_getRefund(){
      this.$fire({
        title: "Deposit refunded complete!",
        type: "success",
        timer: 3000
      })
    },

    failAlert_getRefund(){
      this.$fire({
        title: "Failed to get the deposit",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },

    hideModals () {
      this.$bvModal.hide('modal-multi-11')
      this.$bvModal.hide('modal-multi-12')      
    },
