      /* 아래는 하영이 코드가 미완성이라, 프론트 단 보증금 테스트를 위해 임시적으로 짠 것임*/ 
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      } 
      res = await this.participate(product_id)
