      //this.deposit_money_status = false
    },
    checkDeposit(){ //보증금을 냈는지 안냈는지 여부를 확인하는 함수
      if (1){ //bidders table이 empty라면 **혜송이 여기에 디비쿼리 관련해서 조건 달아줘!
        if (1){ //블체에서 불러온 값이 false라면 **하영이 여기에 블체에서 확인하는거 관련해서 조건 달아줘!
          return false// 아직 보증금 안낸것이므로 false
        }
        else{
          return true //보증금만 내고 입찰은 시도하지 않아 bidders테이블에 값이 없지만 보증금은 냈으므로 true
        }
      }
      else{
        return true //입찰도 시도해서 bidders 테이블에 해당 내역이 있으므로 true
      }
    }, 
    successAlert_finish() {
      this.$fire({
        title: "Successfully Auction Finished!",
        type: "success",
        timer: 3000
      })
    },
    failAlert_finish() {
      this.$fire({
        title: "Failed to finish the auction",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },   

    handleSubmit () {
    // Push the name to submitted names
      this.submittedNames.push(this.bid_price)
    // Hide the modal manually
      this.$nextTick(() => {
        this.$bvModal.hide('modal-prevent-closing')
      })
    }
  },
  
  created () {
    this.loadInterests() // 관심경매
    this.loadBids() // 입찰했고, 진행중인 경매
    this.loadOpened() // 연 경매중 진행중인 경매 - 이게 고쳐줘야하는 부분 1
    this.loadDepositWinningPrice() //낙찰금 송금해야하는 경매
    this.loadWaitRefund() // 보증금 환불 대기 경매
    this.loadBidsFinished() // 입찰한 경매중 종료된 경매
    this.loadGetSuccessMoney() // 낙찰금 입금 대기 경매
    this.loadWaitDepositWithdraw() //보증금 환불이 완료되기를 기다리고있는 경매
    this.loadOpenedFinished() // 연 경매중 종료된 경매 - 이게 고쳐줘야하는 부분 2
  }
}

</script>


