import Vue from 'vue'
import VueRouter from 'vue-router'
import mainPage from '../pages/mainPage.vue' // 메인 페이지 임포트
import productPage from '../pages/productPage.vue' // 물건 출력하는 페이지 임포트
import myPage from '../pages/myPage.vue' // 마이페이지 임포트
import Login from '../pages/login.vue' // 로그인 임포트
import Signup from '../pages/signup.vue' // 회원가입 임포트

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: mainPage
  },
  {
    path: '/productpage',
    component: productPage
  },
  {
    path: '/login', //로그인 페이지
    component: Login,
  },
  {
    path: '/signup', //회원가입 페이지
    component: Signup
  },
  {
    path: '/mypage',
    component: myPage,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


export default router


