<template>
  <div class="container"><br><br>
    <h2>Login</h2><br>
    <b-form @submit.prevent="login"> <!-- 사용자가 입력한 id와 패스워드: id_param, password_param-->
      <b-input type="text" v-model="user.id" placeholder="ID" required/>
      <b-input type="password" v-model="user.pw" placeholder="Password" required/><br><br>
      <b-button type="submit" value="Login">Login</b-button>
    </b-form>
    <br><br><br>
    <h6> Are you not a user?</h6>
    <b-button v-on:click="link">Signup</b-button> <!-- 버튼 누르면 signup 페이지로 넘어감 -->
    <br><br><br>
  </div>
</template>

<script>
  export default {
    data() {
      return { //아이디, 비번 변수
        user: {
          id: '', 
          pw: ''
        }
      }
    },
    beforeCreate() {
      if (this.$session.exists()) {
          alert('이미 로그인된 사용자입니다.')
          this.$router.push('/')
      }
    },
    methods: {
        link: function (event){ // 페이지 이동 이벤트 함수
            this.$router.push('/signup')
        },
        login() { // 로그인함수
          this.$http.post('/api/account/login', {
            user: this.user
          }).then(res => {
            if (res.data.success) {
              this.$session.start()
              this.$session.set('user', res.data.user_no)
              location.href="/"
            }
            else {
              this.failAlert()            
            }
          }).catch(function (err) {
            alert(`${err} in login function`)
          })
        },
        failAlert() {
          this.$fire({
            title: "로그인 실패",
            text: "다시 시도해보세요.",
            type: "error",
            timer: 3000
          })
        },
    }
  }

</script>

<style scoped src="@/assets/styles/styles.css">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  margin: 20px;
}
</style>


