<template>
  <div class="container"><br><br>
    <h2>Sign Up</h2>
    <b-form @submit.prevent="signup"> <!-- 사용자가 입력한 id와 패스워드: username_param, password_param-->
      <b-input type="text" v-model="user.id" placeholder="ID" required/>
      <b-input type="password" v-model="user.pw" placeholder="Password" required/><br><br>
      <b-button type="submit">Sign Up</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        user: { // 회원 가입 정보
          id: '',
          pw: ''
        },
      }
    },
    methods: {
      signup() {  // 회원 가입 함수
        this.$http.post('/api/signup', {
          user: this.user
        }).then(res => {
          if (res.data.success) {
            this.successAlert()
          } else {
            this.failAlert_overlay()
          }
        }).catch(err => {
          this.failAlert()
        })
      },
      successAlert() {
        this.$fire({
          title: "SignUp Success",
          type: "success",
          timer: 3000
        })
      },
      failAlert() {
        this.$fire({
          title: "SignUp Failed",
          text: "Please try again.",
          type: "error",
          timer: 3000
        })
      },
      failAlert_overlay() {
        this.$fire({
          title: "Same ID exists",
          text: "Please try again.",
          type: "error",
          timer: 3000
        })
      },      
    },
  }

</script>

<style scoped src="@/assets/styles/styles.css">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  margin: 20px;
}
</style>


