<template>
    <div>
        <!-- Product Section-->
        <section class="page-section products">
            <div class="container">
            <!-- Products Section Heading-->
            <h2 class="page-section-heading text-center text-uppercase text-secondary mb-0">Products</h2>
            <!-- Icon Divider-->
            <div class="divider-custom">
                <div class="divider-custom-line"></div>
            </div>
            <b-select v-model="selected" v-on:change="setCategory(selected)" size="sm">
              <option v-for="c in category_options" v-bind:key="c.value">{{c.text}}</option>
            </b-select>
            <br><br><br>
              <!-- 물건 목록 -->
              <div class="row">
                  <div class="col-md-6 col-lg-4 mb-5" v-for="(item, index) in productlist" v-bind:key="item.item_id">
                      <div class="products-item mx-auto">
                          <div @click="showModal(item.item_id)"> <!-- 클릭하면 showModal 함수로 가서 해당 물건의 id를 저장함 -->
                            <img v-b-modal.modal-multi-11 class="img-fluid" :src="item.img_url"> <!-- 여기서 img들 보여주는 것-->
                          </div>     
                      <div v-if="isItemClicked(item.item_id)"> <!--isItemClicked함수를 이용해 해당 id가 클릭된 경우에만 모달 실행(if문)-->
                        <b-modal id="modal-multi-11" size="lg" title="Detail Page" ok-only ok-variant="secondary" ok-title="Close">
                          <img width=230 height=280 :src="item.img_url">
                          <p class="my-2"><strong>Category</strong>: {{category_options[item.category_code].text}}</p>
                          <p class="my-2"><strong>Name</strong>: {{item.name}}</p>
                          <p class="my-2"><strong>Reserved Price</strong>: {{item.reserve_price}} ETH</p>
                          <p class="my-2"><strong>Close Date</strong>: {{item.close_date}}</p>
                          <p class="my-2"><strong>Current Highest Bid</strong>: 
                            <span v-if="item.max_bid"><strong style="color: red">{{item.max_bid}} ETH</strong></span>
                            <span v-else><strong style="color: red">nothing</strong></span>
                          </p>
                          <p class="my-2"><strong>Description</strong>: {{item.description}}</p>
                          <!--<b-button v-if="checkDeposit()" v-b-modal.modal-multi-12>입찰하기</b-button>-->
                          <b-button v-if="item.flag" variant="primary" v-b-modal.modal-multi-12>Bid</b-button>
                          <b-button v-else v-b-modal.modal-multi-13>Join</b-button>
                          <b-button class="btn_favorite" @click="addFavorite()">Add to Favorite</b-button>
                        </b-modal>
                       <b-modal id="modal-multi-12" title="Bid" ok-only ok-variant="secondary" ok-title="Close">
                          <p class="my-2">How much do you want to Bid?</p>
                          <form ref="form" @submit.prevent="bidding(index, item)">
                              <b-form-group label-for="bid_price-input">
                              <b-form-input id="bid_price-input" v-model="bid_price" required></b-form-input> <!--bid price 입력받는 곳 -->
                              </b-form-group>    
                              <b-button type="submit" size="sm">Bid</b-button> <!--여기서 입찰함수 실행-->
                          </form>
                        </b-modal>
                        <b-modal id="modal-multi-13" title="Join" ok-only ok-variant="secondary" ok-title="Close">
                          <strong><p class="my-2">Are you sure?</p></strong>
                          <p>If you want to join in the auction, you have to pay on account deposit. The deposit amount is 1ETH. <br> If you press the join button, the deposit will be withdrawn.</p>
                          <b-button class="btn_favorite" @click="depositMoney(index, item.item_id, item.contract, item.reserve_price)">Join</b-button>
                        </b-modal>
<!--
                        <b-modal id="modal-multi-13" size="sm" title="입찰하기" ok-only>
                          <p class="my-1">입찰이 완료되었습니다!</p>
                          <p> 입찰 금액: {{ bid_price }}ETH</p>
                        </b-modal>
-->                        
                      </div>
                    </div>
                </div>
              </div>
            </div>
        </section>
    </div>
</template>

<!-- Bootstrap core JS-->
        <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.5.1/jquery.min.js"></script>
        <script src="https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/js/bootstrap.bundle.min.js"></script>
        <!-- Third party plugin JS-->
        <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery-easing/1.4.1/jquery.easing.min.js"></script>
        <!-- Contact form JS-->
        <script src="assets/mail/jqBootstrapValidation.js"></script>
        <script src="assets/mail/contact_me.js"></script>
        <!-- Core theme JS-->
        <script src="js/scripts.js"></script>

<script>
// import web3 from '../../contracts/web3'
// import auction from '../../contracts/AuctionInstance'
// import auctionBox from '../../contracts/AuctionBoxInstance'
const moment = require('moment');
require('moment-timezone');
moment.tz.setDefault("Asia/Seoul");

export default { // 여기서 아이템 불러옴
  data () {
    return {
      productlist: [],
      bid_price: 0,
      selected_item: '',  // item id 불러오기 위함    
      ids: [], // 모달 중복띄움 방지를 위해 저장하는 id 배열
      category_options: [
          { value: 0, text: 'All' },
          { value: 1, text: 'Electronics' },
          { value: 2, text: 'Fashion' },
          { value: 3, text: 'Health & Beauty' },
          { value: 4, text: 'Motors' },
          { value: 5, text: 'Collectibles' },
          { value: 6, text: 'Sports' },
          { value: 7, text: 'Home & Garden' }
      ],
      current_category: 0,
      deposit_money_status: false
    }
  },
  methods: {
    loadProducts () { // 위에서 출력할때 쓰일 함수
      const user_id = this.$session.exists() ? this.$session.get('user') : null
      this.$http.post('/api/products',{
        user_id: user_id
      }).then(res => {
        this.productlist = res.data
      }).catch(err => {
        alert(`${err}in products function`)
      })
    },
    setCategory(cat){ // 선택한 현재 카테고리 셋팅(current_category 변수에 넣음)
      for (var i=0; i<this.category_options.length; i++){
        if (this.category_options[i].text == cat){
          this.current_category = this.category_options[i].value
        }
      }
      var id = this.current_category
      if (id == 0) {
        this.loadProducts()
      }
      else {
        const user_id = this.$session.exists() ? this.$session.get('user') : null
        this.$http.post(`/api/products/${id}`, {
          user_id: user_id
        }).then(res => {
        this.productlist = res.data
      }).catch(err => {
        alert(`${err} in show function`)
      })
      }
    },  
    // getImgUrl (url) { // 이미지의 url을 data.json에서 불러오는 함수
    //   return url
    // },
    showModal (id) { // 클릭한 id를 저장하는 함수
      this.ids = [] // 우선 이전의 기록을 지우기 위해 배열 초기화
      this.ids.push(id) // 클릭된 id를 push
    },
    isItemClicked (id) { // 해당 id가 클릭되었는지 검사하는 함수
      let test = false // 우선 false로 초기화
      this.ids.forEach(i => {
        if (i === id) { // 만약 i와 id가 같다면
          test = true // true로 고치기
          this.selected_item = id
          return test // test값 반환 (true이면 위의 if문에서 모달이 제대로 출력됨)
        }
      })
      return test
    },
    /*
    submitProduct () { // 등록한 물건에 대한 정보 변수들에 저장하는 함수
      this.name = this.$refs.name.value
      this.price = this.$refs.price.value
      this.date = this.$refs.date.value
      this.time = this.$refs.time.value
      this.img_url = this.$refs.img_url.valule
    },
    */
    resetModal () {
      this.bid_price = ''
      this.bid_priceState = null
    },

    getParticipantId() {
      return new Promise((resolve, reject) => {
        this.$http.post('/api/auction/participate/id', {
          user_id: this.$$session.get('user'),
          item_id: this.selected_item
        }).then(res => {
          if (res.data.success) {
            resolve(res.data.participant_id)
          } else {
            throw new Error("getParticipantId - request failed")
          }
        }).catch(err => {
          reject(err)
        })
      })
    },

    async bidding(index, item) {     // 입찰 함수
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      }

      const now_date = moment().format('YYYY-MM-DD HH:mm:ss')
      if (now_date >= item.close_date) {
        //console.log('1')
        this.failAlert_expiredbid()
      }

      if (this.bid_price <= item.max_bid) {
        //console.log('2')
        this.failAlert_maxbid()
      }

      if (this.bid_price <= item.reserve_price) {
        //console.log('3')
        this.failAlert_reservebid()
      }      
       
      if (this.checkDeposit()) {   // 보증금 체크
        //console.log('4')
        this.depositMoney()
      }

      const participant_id = await this.getParticipantId()
      this.$http.post('/api/auction/bid',{
        user_id: this.$session.get('user'),
        item_id: this.selected_item,
        participant_id: participant_id,
        value: this.bid_price,
        address: '1111' // 여기에 참여자가 보증금 낸 지갑주소 적어주셈
      }).then(res => {
        if (res.data.success) {
          this.$set(this.productlist[index], 'max_bid', this.bid_price)
          this.successAlert_bid()
          this.$bvModal.hide('modal-multi-11')
          this.$bvModal.hide('modal-multi-12')
        } else {
          this.failAlert_bid()
        }
      }).catch(err => {
        this.failAlert_bid()
      })
    },

    addFavorite() { //관심 경매 버튼 클릭시 실행되는 함수
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      } 
      this.$http.post('/api/interests/register',{
        user_id: this.$session.get('user'),
        item_id: this.selected_item
      }).then(res => {
        if (res.data.success) {
          this.hideModals()
          this.successConfirm_interest()
        } else {
          this.failAlert_interest_overlapped()
        }
      }).catch(err => {
        //alert(err)
        this.failAlert_interest()
      })
    },    

    async depositMoney(index, product_id, contract_num, product_price) { //경매 참여하기 누르면 실행되는 함수
      /* 아래는 하영이 코드가 미완성이라, 프론트 단 보증금 테스트를 위해 임시적으로 짠 것임*/ 
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      } 
      this.$http.post('/api/auction/participate', {
        participate: {
          user_id: this.$session.get('user'),
          item_id: this.selected_item,
          price: product_price,
          contract: contract_num
        }
      }).then(res => {
        if (res.data.success) {
          this.successAlert_deposit()
          this.$set(this.productlist[index], 'flag', 1) 
        }
      }).catch(err => {
        console.log(err)
        this.failAlert_deposit()
      })
      /* 여기까지 */

      // product_price: 최소가격
      // product_id: id값
      // contract_num: 계약주소

      // const myAuction = auction(contract_num);
      // const fromAddress = web3.eth.accounts.givenProvider.selectedAddress;
      // console.log(fromAddress)
      // console.log(contract_num)
      // const fa = 0;
      // const bidPriceWei = product_price;
      // console.log(bidPriceWei)
      // web3.eth.getAccounts()
      //     .then((accounts) => {
      //       this.fa = accounts
      // })
      // myAuction.methods
      //   .receiveDeposit()
      //   .send({
      //     from:  fromAddress,
      //     value: bidPriceWei
      //   })
      //   .then(()=>{
      //     console.log(this.deposit_money_status)
      //     this.participate(product_id) //메소드에서 참여자들 데이터베이스에 저장하고 있음
      //     this.deposit_money_status = true;
      //     console.log(this.deposit_money_status)
      //     if (this.deposit_money_status === true){ //정상적으로 보증금 입금 완료되었을 경우
      //       this.$set(this.productlist[index], 'flag', 1)          
      //       this.successAlert_deposit()
      //       this.$bvModal.hide('modal-multi-11')
      //       this.$bvModal.hide('modal-multi-13')
      //     }
      //     else{
      //       this.failAlert_deposit()
      //     }
      //   })
      //   .catch((err) => {
      //       console.log(err)
      //   })

      //this.deposit_money_status =  //deposit_money_status에 현재 보증금 입금 여부를 저장. **하영아 여기에 함수 압력해줘!!
      //this.deposit_money_status = false
    },
    checkDeposit(item_id){ //보증금을 냈는지 안냈는지 여부를 확인하는 함수
      this.$http.post('/api/auction/participate/check', {
        user_id: this.$session.get('user'),
        item_id: item_id
      }).then(res => {
        if (res.data.success) {
          return true
        
        // if (!res.data.verified){ //bidders table이 empty라면 **혜송이 여기에 디비쿼리 관련해서 조건 달아줘!
        //   if (this.deposit_money_status == false){ //블체에서 불러온 값이 false라면 **하영이 여기에 블체에서 확인하는거 관련해서 조건 달아줘!
        //     console.log("deposit_money_status is false now...")
        //     return false// 아직 보증금 안낸것이므로 false
        //   }
        //   else{
        //     return true //보증금만 내고 입찰은 시도하지 않아 bidders테이블에 값이 없지만 보증금은 냈으므로 true
        //   }
      }
        else{
          return false 
       }                
      })
    }, 
    participate() {   // 경매 참여자 DB 저장 함수 (현재 미사용)
      this.$http.post('/api/auction/participate', {
        participate: {
          user_id: this.$session.get('user'),
          item_id: this.selected_item,
          price: product_price,
          contract: contract_num
        }
      }).then(res => {
        if (res.data.success) {
          return true
        }
      }).catch(err => {
        console.log(err)
        this.failAlert_deposit()
        return false
      })
    },
    successAlert_bid() {
      this.$fire({
        title: "Bidding completed!",
        type: "success",
        timer: 3000
      })
    },
    failAlert_bid() {
      this.$fire({
        title: "Failed bidding",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },    
    failAlert_maxbid() {
      this.$fire({
        title: "Failed bidding",
        text: "The price should exceed current highest price",
        type: "error",
        timer: 3000
      })
    },
    failAlert_reservebid() {
      this.$fire({
        title: "Failed bidding",
        text: "The price is lower than reserved price",
        type: "error",
        timer: 3000
      })
    },        
    failAlert_expiredbid() {
      this.$fire({
        title: "Expired Auction",
        text: "The auction is overdue",
        type: "error",
        timer: 3000
      }).then(() => location.reload())
    },         
    successConfirm_interest() {
      this.$fire({
        title: "Add to Favorites completedd!",
        type: "success",
        confirmButtonText: 'Go to Favorites',
        showCancelButton: true
      }).then(result => {
        if (result.value) {
          this.$router.push('/mypage')
        }
      })
    },
    successAlert_deposit() {
      this.$fire({
        title: "Deposit completed!",
        type: "success",
        timer: 3000
      })
    },
    failAlert_interest_overlapped() {
      this.$fire({
        title: "Already in Favorites!",
        type: "error",
        timer: 3000
      })
    },
    failAlert_interest() {
      this.$fire({
        title: "Add to Favorites failed",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },
    failAlert_deposit() {
      this.$fire({
        title: "Failed to deposit",
        text: "Please try again",
        type: "error",
        timer: 3000
      })
    },   

    hideModals () {
      this.$bvModal.hide('modal-multi-11')
      this.$bvModal.hide('modal-multi-12')      
    }
  },
  created () {
    this.loadProducts()
  }
}
</script>


<style scoped src="@/assets/styles/styles.css">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  margin: 20px;
}
</style>


